<template>
	<div ref="myElement">
		<div id="player-container">
			<div class="watermark"></div>
		</div>
	</div>
</template>
<script>
import { ref, onMounted, watch } from 'vue'
export default {
	name: 'MyVideoPlayer',
	props: ['medium'],

	data() {
		return {
			player: null,
			jianting: '',
		}
	},

	methods: {
		initializePlayer() {
			if (this.player) {
				// 销毁旧播放器实例
				this.player.dispose()
			}
			this.player = new window.Aliplayer({
				id: 'player-container',
				source: this.medium,
				autoplay: true,
				width: `${this.videoWidth}px`,
				height: `${this.videoHeight}px`,
				isLive: false,
				autoPlayDelay: 3,
				autoPlayDelayDisplayText: '请等待...',
			})
			this.player.on('ended', () => {
				// 在这里可以执行你想要的操作，比如播放下一个视频
				console.log('视频播放完毕')
				this.$emit('custom-ended', true)
			})
		},
		// 播放完毕  应该播放另一个 从列表里面获取另一个
	},
	mounted() {
		this.initializePlayer()
	},
	computed: {
		videoHeight() {
			const element = this.$refs.myElement
			const width = element.offsetWidth
			const videoAspectRatio = 16 / 9 // 假设视频宽高比例是 16:9
			return width / videoAspectRatio
		},
		videoWidth() {
			const element = this.$refs.myElement
			return element.offsetWidth
		},
	},
	watch: {
		medium(newMedium) {
			// 在 medium 属性发生变化时重新初始化播放器
			this.initializePlayer(newMedium)
		},
	},
}
</script>
<style>
#player-container {
	position: relative;
}

.watermark {
	position: absolute;
	top: 10px;
	right: 10px;
	width: 100px;
	height: 50px;
	opacity: 0.7;
	/* 设置水印图片的背景 */
	background: url('./../assets/logo.svg'); /* 替换成水印图片的路径 */
	/* 这里可以继续添加其他样式，比如文字水印等 */
}
</style>
